// Este fichero contendr una serie de funciones que vamos a estar utilizando todo el tiempo en otros mdulos.
import { TIMEOUT_SEC } from './config.js';

// esto es una funcion que devuelve una promesa que tiene un setTimeout.
const timeout = function (s) {
  return new Promise(function (_, reject) {
    setTimeout(function () {
      reject(new Error(`Request took too long! Timeout after ${s} second`));
    }, s * 1000);
  });
};

/////// Refactorizemos las funciones getJSON y sendJSON en una sola funcin
export const AJAX = async function (url, uploadData = undefined) {
  try {
    // Utilizamos un condicional para la variable que cambia de valor segn la llamada que sea, get o send.
    const fetchPro = uploadData
      ? fetch(url, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(uploadData),
        })
      : fetch(url);

    // Utilizamos el timeout y ejecutamos el Promise.race para que la primera funcin asncrona que se ejecute paralize a la otra.

    const res = await Promise.race([fetchPro, timeout(TIMEOUT_SEC)]);
    const data = await res.json();

    if (!res.ok) throw Error(`${data.message} ${res.status}`);
    return data;
  } catch (err) {
    // Para poder controlar el error que se produce aqu en la peticin en el fichero model.js que es desde donde se llama a esta funcin, cuando se produzca el error aqu lo atrapamos con el catch y generamos un nuevo error en el catch con thorow. Con esto forzamos a que la promesa que devuelve la funcin asncrona getJSON ser rechazada y la podremos tratar en el catch del fichero que la llam.
    throw err;
  }
};

